import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import Modal from './Modal'

import image1 from '../assets/gal1.jpg';
import image2 from '../assets/gal2.jpg';
import image3 from '../assets/gal3.jpg';
import image4 from '../assets/gal4.jpg';
import image5 from '../assets/gal5.jpg';
import image6 from '../assets/gal6.jpg';
import image7 from '../assets/gal7.jpg';
import image8 from '../assets/gal8.jpg';

const useStyles = makeStyles((theme) => ({
  container: {
    padding: '5px 300px',
  },
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
   
    
  },
  title: {
    textAlign: 'left',
    fontSize: '1.5em !important'
  },
  gridList: {
    width: '100%',

  },
}));




const tileData = [
  {
    img: image1,
    title: 'Image',
    author: 'author',
    cols: 1,
  },
  {
    img: image2,
    title: 'Image',
    author: 'author',
    cols: 1,
  },
  {
    img: image3,
    title: 'Image',
    author: 'author',
    cols: 1,
  },
  {
    img: image4,
    title: 'Image',
    author: 'author',
    cols: 1,
  },
  {
    img: image5,
    title: 'Image',
    author: 'author',
    cols: 1,
  },
  {
    img: image6,
    title: 'Image',
    author: 'author',
    cols: 1,
  },
  {
    img: image7,
    title: 'Image',
    author: 'author',
    cols: 2,
  },
  {
    img: image8,
    title: 'Image',
    author: 'author',
    cols: 1,
  },
];

export default function Gallery() {
  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [img, setImg] = React.useState(null);

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = (imgObj) => {
      setImg(imgObj.img)
      setOpen(true)
  }

  return (
    <div className={classes.container}>
       <h2 className={classes.title}>Galerija</h2>
        <div className={classes.root}>
          <GridList cellHeight={500} className={classes.gridList} cols={4}>
            {tileData.map((tile) => (
              <GridListTile key={tile.img} cols={tile.cols || 1}>
                <img src={tile.img} alt={tile.title} onClick={() => {handleOpen(tile)}}/>
              </GridListTile>
            ))}
            {open ? <Modal open={open} handleClose={handleClose} img={img}/> : null}
          </GridList>
        </div>
    </div>
  );
}