import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import img from '../assets/profil.jpg'
import AssignmentIcon from '@material-ui/icons/Assignment'
import VideocamIcon from '@material-ui/icons/Videocam'
import PhotoIcon from '@material-ui/icons/Photo'
import MailIcon from '@material-ui/icons/Mail'
import Gallery from './Gallery'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  headline: {
    fontSize: '8em',
    fontStyle: "italic",
    // fontFamily: ""
  },
  subHeadline: {
    fontSize: '4em',
    fontStyle: "italic",
  },
  biography: {
    padding: theme.spacing(2),
    textAlign: 'left',
    // fontSize: '1.2em',
    color: theme.palette.text.primary,
    padding: "10px 300px",
    textAlign: "justify",
    backgroundColor: '#f5f5f5'
  },
  imgContainer: {
    // backgroundColor: 'rgb(22,95,152)'
  },
  profileImage : {
    width: '70%',
    margin: '15%',
    borderRadius: '100%',
   
  },
  button : {
    margin: "30px 20px 63px",
    fontSize: "20px",
    padding: "15px 40px"
  },
  icon: {
    marginRight: 10
  }

}));

export default function CenteredGrid() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container spacing={0}>
        <Grid item xs={4}>
          <Paper className={classes.imgContainer}>
            <img src={img} className={classes.profileImage}/>
          </Paper>
        </Grid>
        <Grid item xs={8}>
          <Paper className={classes.paper}>
            <h1 className={classes.headline}>Đurađ Milanović</h1>
            <h2 className={classes.subHeadline}>...</h2>
             <Button variant="contained" color="primary" className={classes.button}>
               <AssignmentIcon className={classes.icon}/>Biografija</Button> 
             <Button variant="contained" color="primary" className={classes.button}>
               <VideocamIcon className={classes.icon}/>U medijima</Button>
             <Button variant="contained" color="primary" className={classes.button}>
               <PhotoIcon className={classes.icon}/>Galerija</Button>
             <Button variant="contained" color="primary" className={classes.button}>
               <MailIcon className={classes.icon}/>Kontakt</Button>
          </Paper>
        </Grid>
        <Grid item xs={12}>
          <Paper className={classes.biography}>
            <h2>Biografija</h2>
            <p>Rođen 1984. godine u Somboru. Osnovnu školu je završio u Čonoplji, 
            Srednju poljoprivredno-prehrambenu školu u Somboru. 
            Zvanje profesora razredne nastave stekao je na Pedagoškom fakultetu u Somboru 2011.
            godine. Diplomirao je na predmetu Metodika nastave matematike sa ocenom deset.</p>
            <p>Od 2007. godine radio je kao agent prodaje u preduzeću za distribuciju robe 
            široke potrošnje “Nelt” iz Beograda.</p>
            <p> Od 2011. godine je zaposlen u OŠ “Avram Mrazović” kao profesor razredne 
            nastave, prilikom rada u nastavi se dodatno usavršavao.</p>
            <p>Odbornik je u Skupštini grada Sombora od 2012. godine i od tada je obavljao 
            funkciju predsednika Odbora za prosvetu i kulturu skupštine grada Sombora.</p>
            <p>Funkciju člana Gradskog veća grada Sombora zaduženog za oblast obrazovanja 
            vršio je od 2012. do 2014. godine. Na njegovu inicijativu je uvedeno 
            fizičko-tehničko obezbeđenje u svim osnovnim školama u gradu, 
            izvršena kapitalna ulaganja na objektima obrazovnih ustanova 
            koje su godinama bile u lošem stanju. Aktivno je učestvovao
             u reformi mreže škola grada Sombora i na njegovu inicijativu 
             je ustanovljena nagrada iz oblasti obrazovanja grada Sombora “Avram Mrazović”.</p>
            <p>Od avgusta 2016. godine vrši funkciju direktora Edukativnog centra za obuke u 
            profesionalnim i radnim veštinama.</p>
            <p>Istakao se u projektima međunarodne saradnje sa predstavnicima više ambasada i 
            kulturnih ustanova.</p>
            <p>Govori engleski jezik.</p>
            <p>Oženjen je, otac troje dece.</p>
          </Paper>
        </Grid>
        <Grid item xs={12}>
          <Paper >
            <Gallery/>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
